* {
  box-sizing: border-box;
}

body {
  margin: 0;

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  padding: 15px;
  display: flex;

  margin-top: -10px;
  margin-left: -10px;

  margin: 0 auto;
}

.gallery-link {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-decoration: none;
  color: #212121;
}

.photo-card {
  margin-bottom: 30px;
  display: flex;

  align-items: stretch;

  scale: 1;

  flex-basis: calc(100% / 4 - 15px);
  margin-top: 15px;
  margin-left: 15px;

  border-radius: 9px;
  box-shadow: 1px 1px 5px 1px rgba(5, 5, 5, 0.164);

  transition: transform 250ms linear;
}

.photo-card:hover {
  transform: scale(1.01);
  box-shadow: 0px 1px 1px rgb(0 0 0 / 12%), 0px 4px 4px rgb(0 0 0 / 6%),
    1px 4px 6px rgb(0 0 0 / 16%);

  transition: box-shadow 250ms ease;
  transition: transform 250ms ease;
}

.photo-card img {
  width: 100%;
}

.info {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 15px;
}

.info-item {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
}

.search-form {
  display: flex;
  justify-content: center;
  align-items: center;

  height: 60px;
  width: 100%;

  background: linear-gradient(to right, rgb(109, 47, 255), rgb(100, 123, 255));
}

.search-button {
  height: 35px;

  cursor: pointer;

  outline: none;
  border: 1px solid rgb(173, 173, 173);
  border-top-right-radius: 13px;
  border-bottom-right-radius: 13px;
  border-left: none;
}

.search-button:hover {
  background-color: rgb(113, 135, 255);
  border-color: rgb(113, 135, 255);
}

.search-input {
  padding: 2px 7px;
  height: 35px;

  outline: none;
  border: 1px solid rgb(173, 173, 173);
  border-top-left-radius: 13px;
  border-bottom-left-radius: 13px;
  border-right: none;
}

.load-more {
  margin: 10px auto 70px;
  padding: 15px 40px;

  outline: none;
  background: linear-gradient(to right, rgb(109, 47, 255), rgb(100, 123, 255));
  color: white;
  border: none;
  border-radius: 7px;

  cursor: pointer;

  text-transform: uppercase;
  font-weight: 700;
  font-size: 14px;
}

.load-more:hover {
  box-shadow: 1px 1px 5px 1px rgba(5, 5, 5, 0.253);
  font-size: 15px;
}

